.uk-form--content{
  border-radius: 20px;
  padding: $uk-box-padding;

  @include media-breakpoint-down(md){
    box-shadow: none;
    padding: 0;
  }
  &-form{
    margin-top: 60px;
    display: flex;
    @include media-breakpoint-down(md){
      flex-wrap: wrap;
    }
    label{
      color: #AFAFAF;
      font-size: 15px;
      margin-bottom: 10px;
    }
    &-part{

      &:first-child, &:last-child{
        @include media-breakpoint-down(md){
          max-width: 100%;
          flex: 0 0 100%;
        }
      }
      &:first-child{
        border-right: 1px solid #ced4da;
        padding-right: 40px;
        max-width: 60%;
        flex: 0 0 60%;
        @include media-breakpoint-down(md){
          border: 0;
          padding-right: 0;
        }
      }
      &:last-child{
        padding: 60px 40px;
        max-width: 40%;
        flex: 0 0 40%;
        @include media-breakpoint-down(md){
          padding: 0;
          margin-top: 30px;
        }
      }
    }
  }
}
.uk-radios{
  display: flex;
  align-items: center;
  margin-top: 12px;
  .uk-radio{
    margin-right: 30px;
    &:last-child{
      margin-right: 0;
    }
  }
}
.uk-form-button{
  margin-top: 30px;
}
.uk-delete-buttons{
  display: flex;
  flex-flow: column;
  margin-top: 30px;
  .btn{
    margin-bottom: 30px;
    padding-left: 10px;
    padding-right: 10px;
    i{
      margin-right: 10px;
    }
    &:last-child{
      margin: 0;
    }
  }
}