$base-size: 16;
@function size($target, $context: $base-size) {
  @return ($target / $context) * 1rem;
}

// Colors
$uk-primary-color: #DF342F;
$uk-secondary-color: #fff;
$uk-body-text-color: #4D4D4D;

//  Layout
$uk-container__lg: 1610px;

// Sections
$uk-home-section-padding: 135px;
$uk-home-section-padding--mobile: 60px;
$uk-page-section-padding: 100px;
$uk-page-section-padding--mobile: 45px;

//  Boxes
$uk-box-padding: 35px;

//  Typography
$uk-font-family: Arial;
$uk-body-font-size: 14;
$uk-h1-font-size: 60;
$uk-h2-font-size: 22;
$uk-h3-font-size: 22;

//  Header
$uk-header-height: 90px;

// Buttons
$uk-btn-font-size: 16px;
$uk-btn-border-radius: 30px;
$uk-btn-padding-x: size(30);
$uk-btn-padding-y: size(8);

//  Inputs
$uk-input-padding-y: size(10);
$uk-input-padding-x: size(20);

//  Footer
$uk-footer-bg: #2B2A2A;

//  Mobile
$uk-mobile-layout-padding-x: 25px;
