.uk-header{
  font-size: 18px;
  display: flex;
  align-items: center;
  height: $uk-header-height;
  -webkit-box-shadow: 0px 2px 5px 0px rgba(212,212,212,0.54);
  -moz-box-shadow: 0px 2px 5px 0px rgba(212,212,212,0.54);
  box-shadow: 0px 2px 5px 0px rgba(212,212,212,0.54);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 99;
  background-color: rgba($white, .8);
  @include media-breakpoint-down(lg){
    font-size: 18px;
  }
  @include media-breakpoint-down(md){
    background-color: $white;
  }
  &--container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: $uk-container__lg;
    width: 100%;
    padding: 0 $grid-gutter-width/2;
    margin: auto;
  }
  &--right{
    display: flex;
    align-items: center;
    margin-left: auto;
    @include media-breakpoint-down(md){
      display: none;
    }
  }
  &--logo{
    display: inline-block;
    img{
      width: 66px;
      height: auto;
    }
  }
}
.uk-menu{
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  &:first-child{
    @include media-breakpoint-down(md){
      order: 2;
    }
  }
  &:last-child{
    @include media-breakpoint-down(md){
      order: 1;
    }
  }
  @include media-breakpoint-down(md){
   flex-flow: column;
   align-items: flex-start;
   width: 100%;
  }
  li{
    @include media-breakpoint-down(md){
      flex-flow: column;
      width: 100%;
      margin: 0;
      border-bottom: 1px solid #EEEEEE;
    }
    margin: 0 12px;
    a{
      @include media-breakpoint-down(md){
        display: block;
        padding: 20px 0;
        margin: 0 15px;
      }
    }

    &.active{
      a{
        color: $uk-primary-color;
      }
    }
  }
  &.uk-header--menu-sign-up{
    font-weight: bold;
    @include media-breakpoint-down(md){
      font-size: 25px;
      border-bottom: 1px solid #EEEEEE;
      padding-bottom: 15px;
    }
    li{
      @include media-breakpoint-down(md){
        border: none;
        a{
          @include media-breakpoint-down(md){
            padding: 10px 0;
            margin: 0;
            color: $uk-primary-color;
          }
        }
      }
    }
  }
  &.uk-header--menu-edit{
    display: none;
    @include media-breakpoint-down(md){
      display: flex;
    }
    li{
      a{
        i{
          margin-right: 15px;
          font-size: 30px;
        }
      }
    }
  }
}
.uk-menu-divider{
  height: 30px;
  width: 2px;
  background-color: #666666;
  margin: 0 12px;
  @include media-breakpoint-down(md){
    display: none;
  }
}
.uk-user-page-link{
  @include media-breakpoint-down(md){
    font-size: 25px;
    color: $uk-primary-color;
  }
  a{
    display: flex;
    align-items: center;
    @include media-breakpoint-down(md){
      color: $uk-primary-color;
    }
    .fa{
      line-height: normal;
    }
    .uk-user-icon{
      color: $uk-primary-color;
      font-size: 30px;
    }
    .uk-caret-icon{
      font-size: 18px;
      padding-top: 5px;
      @include media-breakpoint-down(md){
        display: none;
      }
    }
    span{
      margin: 0 12px;
    }
  }
}
//  Menu Mobile

.uk-menu-open-btn{
  flex-flow: column;
  display: none;
  cursor: pointer;
  padding: 20px 0;
  &.active{
    span{
      transform: rotate(0deg);
      transition: .2s;
      &:nth-child(1){
        transform: rotate(-45deg);
        margin-bottom: 0;
      }
      &:nth-child(2){
        display: none;
      }
      &:nth-child(3){
        transform: rotate(45deg);
        margin-top: -4px;
      }
    }
  }
  @include media-breakpoint-down(md){
    display: flex;
  }
  span{
    display: block;
    width: 30px;
    height: 4px;
    border-radius: 3px;
    background-color: $uk-primary-color;
    margin-bottom: 4px;
  }
}
.uk-menu-section{
 @include media-breakpoint-down(md){
   background-color: $white;
   padding: 60px $grid-gutter-width/2;
   font-size: 20px;
   overflow-y: auto;
   z-index: 99;
 }
  &.active{
    animation: menu_open .2s .05s alternate backwards;
    @include media-breakpoint-down(md){
      display: flex;
      flex-flow: column;
      position: absolute;
      top: $uk-header-height;
      left: 0;
      right: 0;
      height: calc(100vh - #{$uk-header-height});
      background: rgb(255,255,255);
      background: linear-gradient(0deg, rgba(255,255,255,1) 96%, rgba(238,238,238,1) 100%);
    }
  }
}
.no-ovrf{
  @include media-breakpoint-down(md){
    overflow: hidden;
  }
}
@keyframes menu_open {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}