.uk-content-title{
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 0;
  &--span{
    display: inline-block;
    margin: 0 10px;
  }
}
.uk-content{
  &--heading{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.gray-mobile {
  @include media-breakpoint-down(md) {
    background-color: #F7F8F9;
    margin-left: -$grid-gutter-width/2;
    margin-right: -$grid-gutter-width/2;
    padding: 10px $grid-gutter-width/2;
  }
}


