.uk-footer{
  background-color: $uk-footer-bg;
  line-height: 1.8;
  color: rgba($white, .3);
  &--top{
    padding-top: 60px;
    margin-bottom: $grid-gutter-width/2;
    .uk-footer--container{
      border-bottom: 1px solid rgba($white, .3);
      padding-bottom: 60px;
    }
  }
  &--col{
    height: 100%;
    @include media-breakpoint-down(sm){
      text-align: center;
    }
  }
  &--container{
    max-width: $uk-container__lg;
    width: 100%;
    padding: 0 $grid-gutter-width/2;
    margin: auto;
  }
  &--logo{
    margin-bottom: 40px;
    display: inline-block;
    img{
      width: 66px;
      height: auto;
    }
  }
  &--title{
    color: #fff;
    margin-bottom: 30px;
    font-size: 21px;
  }
  &--info{
    margin: 0;
    padding: 0;
    list-style: none;
    font-weight: bold;
    li{
      display: flex;
      margin-bottom: 40px;
      @include media-breakpoint-down(sm){
        justify-content: center;
        margin-bottom: 20px;
      }
      a{
        color: rgba($white, .3);
        &:hover{
          color: $uk-primary-color;
        }
      }
      i{
        margin-right: 20px;
        color: $uk-primary-color;
        font-size: 24px;
      }
      &:last-child{
        margin-bottom: 0;
      }
    }
    &-address{
      span{
        display: block;
      }
    }
  }
  &--social{
    &-parent{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      @include media-breakpoint-down(sm){
        justify-content: center;
      }
    }
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
    font-size: 18px;
    span{
      font-size: 0;
      text-indent: -99999;
    }
    li{
      margin-right: 25px;
      &:last-child{
        margin-right: 0;
      }
      a{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 38px;
        height: 38px;
        background-color: $white;
        color: $uk-footer-bg;
        border-radius: 100%;
        &:hover{
          background-color: $uk-primary-color;
          color: $white;
        }
      }
    }
  }
  &--follow-text{
    font-size: 14px;
    margin-right: 25px;
    @include media-breakpoint-down(sm){
      display: none;
    }
  }
  &--map{
    iframe{
      height: 340px;
      width: 100%;
      max-width: 100%;
    }
  }
  &--copyright{
    @include media-breakpoint-down(sm){
      text-align: center;
    }
  }
}