.uk-tests{
  &--section{
    padding-bottom: $uk-home-section-padding;
    background-position: bottom center;
    background-repeat: no-repeat;
    background-size: 1920px;
    @include media-breakpoint-down(sm){
      padding-bottom: $uk-home-section-padding--mobile;
    }
    @include media-breakpoint-down(sm){
      padding-bottom: 45px;
    }
  }
  &--box{
    background-color: $white;
    border-radius: 20px;
    height: 100%;
    display: flex;
    flex-flow: column;
  }
  &--head{
    padding: $uk-box-padding $uk-box-padding 0 $uk-box-padding;
    &-double{
      display: flex;
      justify-content: space-between;
      .uk-doted-title:last-child{
        border: none;
      }
    }
    .col-30, .col-70{
      @include media-breakpoint-down(sm){
        max-width: 100%;
        flex: 100%;
        padding: 0;
      }
    }
    @include media-breakpoint-down(sm){
      flex-wrap: wrap;
    }
  }
  &--title{
    font-size: 22px !important;
    font-weight: bold;
    margin-bottom: 5px;
  }
  &--text{
    padding: 20px 0;
  }
  &--body{
    padding: 15px $uk-box-padding 0 $uk-box-padding;
  }
  &--price{
    padding: 15px $uk-box-padding;
    font-weight: bold;
    &-inner{
      padding-top: 20px;
      display: flex;
      justify-content: space-between;
      border-top: 1px solid rgba(223, 52, 47, 0.2);
    }
  }
  &--footer{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0 $uk-box-padding 15px $uk-box-padding;
    margin-top: auto;
    .btn{
      @media screen and (max-width: 1400px) and (min-width: 1200px){
          font-size: 16px;
        padding-left: 10px;
        padding-right: 10px;
      }
    }
  }
  &--date{
    padding-top: 10px;
    padding-bottom: 10px;
    border-top: 1px solid #EEEEEE;
    border-bottom: 1px solid #EEEEEE;
    font-size: 13px;
    padding-right: 10px;
    display: flex;
    @include media-breakpoint-down(sm){
      font-size: 12px;
    }
    .fa{
      margin-right: 5px;
    }
    &-part{
     &:last-child{
       margin-left: 20px;
     }
    }
    &.col-70{
      @include media-breakpoint-down(sm){
        max-width: 100%;
        flex: 0 0 100%;
        border-bottom: 0;
        padding-right: 0;
      }
    }
  }
  &--more{
    &.col-30{
      @include media-breakpoint-down(sm){
        max-width: 100%;
        flex: 0 0 100%;
        margin-top: 15px;
      }
    }
  }

}
.uk-doted-title{
  padding-left: 30px;
  font-size: 16px;
  position: relative;
  display: flex;
  align-items: flex-start;
  &:after{
    content: '';
    border-radius: 100%;
    position: absolute;
    display: block;
    background-color: $uk-primary-color;

  }
  &:nth-child(1){
    &:after{
      width: 18px;
      height: 18px;
      opacity: 1;
      left: 0;
      top: 5px;
    }
  }
  &:nth-child(2){
    &:after{
      width: 13px;
      height: 13px;
      opacity: .5;
      left: 3px;
      top: 7px;
    }
  }
  &:nth-child(3){
    &:after{
      width: 10px;
      height: 10px;
      opacity: .25;
      left: 4px;
      top: 10px;
    }
  }
  &:last-child{
    border-bottom: 1px solid rgba($uk-primary-color, .2);
    padding-bottom: 15px;
  }
}
.uk-points{
  display: block;
  font-size: 22px;
  font-weight: bold;
  color: $uk-primary-color;
  @include media-breakpoint-down(sm){
    font-size: 18px;
  }
  &--info{
    @include media-breakpoint-down(sm){
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 15px;
    }
  }
  &--completed{
    display: block;
    font-size: 15px;
  }
}
.uk-completed-test{
  .uk-tests--box{
    background-color: $uk-primary-color;
    color: #fff;
  }
  .uk-doted-title{
    &:after{
      background-color: #fff;
    }
  }
  .uk-points{
    color: #fff;
  }
  .btn{
    color: $uk-primary-color;
  }
}



