.uk-sidebar{
  padding: $uk-box-padding;
  border-radius: 20px;
  height: 100%;
  &--heading{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .uk-sidebar-title{
      margin: 0;
    }
  }
}
.uk-sidebar-title{
  font-size: 22px;
  font-weight: bold;
}