.uk-single-test{
  padding: $uk-box-padding;
  border-radius: 25px;
  &--heading{
    margin-bottom: 30px;
  }
  &--heading-info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px;
    padding-bottom: 10px;
    &-part{
      font-weight: bold;
      font-size: 16px;
    }
  }
  &--title{
    font-size: 27px;
    font-weight: bold;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px;
    padding-bottom: 10px;
  }
}