.uk-news{
  &--section{
    padding-top: $uk-home-section-padding;
    padding-bottom: $uk-home-section-padding;
    background-repeat: no-repeat;
    background-position: left;
    @include media-breakpoint-down(md){
      padding-bottom: $uk-home-section-padding--mobile;
      padding-top: $uk-home-section-padding--mobile;
    }
    @include media-breakpoint-down(sm){
      padding-top: 45px;
      padding-bottom: 45px;
    }
    &-bg{
     background-repeat: no-repeat;
     background-position: 150px center;
     background-size: 184px;
      @media screen and (max-width: 1880px) {
        background-position: 100px center;
      }
      @media screen and (max-width: 1680px) {
        background-position: 40px center;
      }
      @media screen and (max-width: 1780px) {
        background-size: 134px;
      }
      @media screen and (max-width: 1580px) {
        background-size: 95px;
      }
      @media screen and (max-width: 1480px) {
        background-size: 80px;
        background-position: 20px center;
      }
      @media screen and (max-width: 1400px) {
        background-image: none !important;
      }
    }
  }
  &--box{
    background-color: $white;
    display: flex;
    flex-flow: column;
    border-radius: 20px;
    height: 100%;
  }
  &--head{
    height: 180px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &--body{
    padding: 15px $uk-box-padding;
  }
  &--footer{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0 $uk-box-padding 15px $uk-box-padding;
    margin-top: auto;
  }
  &--date{
    padding-top: 15px;
    border-top: 1px solid #EEEEEE;
    font-size: 13px;
    padding-right: 10px;
    .fa{
      margin-right: 5px;
    }
  }
  &--more{
    padding-left: 15px;
    &-link{
      height: 50px;
      width: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid $uk-primary-color;
      border-radius: 100%;
      color: $uk-primary-color;
      font-size: 30px;
      transition: color .2s, background-color .2s;
      &:hover{
        background-color: $uk-primary-color;
        color: $white;
      }
    }
  }
}