.uk-pagination{
  display: flex;
  justify-content: flex-end;
  .uk-pagination--dots{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    outline: none !important;
    li a{
     color: $white;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      background-color: rgba($uk-primary-color, .3);
    }
    li{
      margin: 0 5px;
      &.active{
        a{
          background-color: $uk-primary-color;
        }
      }
    }
  }
}