.uk-tab-content{
  border-radius: 20px;
  padding: $uk-box-padding;
  @include media-breakpoint-down(md){
    box-shadow: none;
    padding: 0;
  }
}
.uk-tabs{
  display: flex;
  margin-top: 20px;
  border-top: 1px solid #eee;
  @include media-breakpoint-down(md){
    flex-wrap: wrap;
    margin-top: 0;
    margin-bottom: 50px;
  }
  &--part{
    margin-top: 30px;
    &:first-child{
      max-width: 15%;
      flex: 0 0 15%;
      @include media-breakpoint-down(md){
        max-width: 100%;
        flex: 0 0 100%;
        display: none;
      }
    }
    &:last-child{
      max-width: 85%;
      flex: 0 0 85%;
      padding: 0 $grid-gutter-width/2 0 $grid-gutter-width;
      @include media-breakpoint-down(md){
        max-width: 100%;
        flex: 0 0 100%;
        padding: 0;
      }
    }
    &-heading{
      margin-bottom: 60px;
      h3{
        font-weight: bold;
        font-size: 22px;
      }
    }
    &-content{
      display: none;
      margin-bottom: 30px;
      &.active{
        display: block;
      }
    }
  }
  &--nav{
    display: flex;
    flex-flow: column;
    height: 100%;
    justify-content: space-between;
    position: relative;
    &-qn{
      display: flex;
      flex: 1;
      color: #AFAFAF;
      position: relative;
      cursor: pointer;
      &.active{
        &:after{
          background-color: $uk-primary-color;
          width: 15px;
          height: 15px;
          flex: 0 0 15px;
          right: 8px;
          top: 0;
        }
        color: $uk-primary-color;
      }
      &:hover{
        color: rgba($uk-primary-color, .8);
      }
      &:after{
        content: '';
        display: block;
        width: 12px;
        flex: 0 0 12px;
        height: 12px;
        background-color: #AFAFAF;
        border-radius: 100%;
        margin-left: 10px;
        margin-top: 4px;
        position: absolute;
        right: 10px;
        top: 0;
      }
      &:before{
        display: block;
        content: '';
        width: 1px;
        background-color: #AFAFAF;
        height: 100%;
        position: absolute;
        right: 15px;
        top: 5px;
      }
    }
  }
  &--navs{
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    position: relative;
    font-size: 18px;
    overflow-y: auto;
    max-height: 600px;
    &-qn{
      display: flex;
      padding: 10px 0;
      border-bottom: 1px solid #eee;
      color: #AFAFAF;
      position: relative;
      cursor: pointer;
      padding-right: 15px;
      &:last-child{
        border: 0;
      }
      &:hover{
        color: $uk-primary-color;
      }
      &.active{
        color: $uk-primary-color;
        &:after{
          content: "\f061";
          font-family: FontAwesome;
          margin-left: auto;
          font-size: 16px;
          font-weight: normal;
        }
      }
    }
  }

}
.uk-option{
  &--text{
    margin-bottom: 10px;
    display: block;
  }
  &--image{
    img{
      width: 50%;
    }
  }
  &--info{
    display: flex;
    flex-flow: column;
    padding: 80px 30px;

  }
  &--time{
    color: $uk-primary-color;
    display: block;
    font-size: 20px;
    font-weight: bold;
  }
  &--point{
    display: block;
    font-size: 22px;
    font-weight: bold;
  }
  &--nav{
    display: flex;
    margin-top: 60px;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 600px) {
      flex-wrap: wrap;
    }
    &-links{
      display: flex;
      margin: 0;
      padding: 0;
      list-style: none;
      @media screen and (max-width: 600px) {
        justify-content: space-between;
      }
      li{
        margin-right: 30px;
        &:last-child{
          margin: 0;
        }
        a{
          display: flex;
          align-items: center;
          color: #AFAFAF;
          &:hover{
            color: $uk-body-text-color;
          }
        }
      }
    }
    &-part{
      &:last-child{
        @media screen and (max-width: 600px) {
          margin-top: 30px;
          text-align: center;
        }
      }
      @media screen and (max-width: 600px) {
        max-width: 100%;
        flex: 0 0 100%;
      }
    }
  }
  label{
    font-weight: bold;
    color: $body-color;
  }
}
.uk-acc-head{
  background-color: #F7F8F9;
  padding: 10px 15px;
  text-align: center;
  font-size: 18px;
  color: #a1a1a2;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  span{
    width: 10px;
    height: 10px;
    display: block;
    border-radius: 100%;
    margin-left: 10px;
    background-color: #a1a1a2;
  }
  &.done{
    background-color: $uk-primary-color;
    color: $white;
    span{
      background-color: $white;
    }
  }
}