
.uk-login-main{
  background: rgb(255,255,255);
  background: linear-gradient(0deg, rgba(255,255,255,1) 50%, rgba(223,52,47,1) 50%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 30px;
  @include media-breakpoint-down(sm){
    background-color: $white !important;
    height: auto;
    padding: 0;
    background-image: url(../images/My-page-decor.png);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 768px;
    display: block;
  }
}
.uk-login-box{
  border-radius: 20px;
  padding: 45px 80px;
  background-color: $white;
  max-width: 540px;
  flex: 0 0 540px;
  margin: auto;
  @include media-breakpoint-down(md){
    max-width: 480px;
    flex: 480px;
    padding: 45px;
  }
  @include media-breakpoint-down(sm){
    box-shadow: none;
    background-color: transparent;
  }
  &--logo{
    display: inline-block;
    margin-bottom: 30px;
    img{
      width: 90px;
    }
  }
  &--nav{
    list-style: none;
    margin: 0 0 60px 0;
    padding: 0;
    display: flex;
    align-items: center;
    font-size: 18px;
    li{
      border-bottom: 2px solid #EEEEEE;
      max-width: 50%;
      flex: 0 0 50%;
      &.active{
        border-color: $uk-primary-color;
        a{
          color: $uk-primary-color;
        }
      }
      &:first-child{
          text-align: left;
      }
      &:last-child{
        text-align: right;
      }
      a{
        display: block;
        padding: 10px;
        color: #909497;
      }
    }
  }
  &--form{
    .uk-form-field{
      margin-bottom: 30px;
    }
    .uk-radios{
      margin-top: 0;
      .styled{
        display: none;
      }
      label{
        margin: 0;
      }
    }
  }
}
.uk-new-password{
  color: $uk-primary-color;
  font-size: 14px;
}