.uk-slider{
  height: 100vh;
  position: relative;
  @include media-breakpoint-down(md){
    height: calc(100vh - #{$uk-header-height});
  }
  @include media-breakpoint-down(sm){
    height: 60vh;
  }
  .owl-dots{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;z-index: 999;
    text-align: center;
    padding-bottom: 30px;
    .owl-dot{
      margin: 0 5px;
      outline: none !important;
      span{
        display: block;
        width: 13px;
        height: 13px;
        border-radius: 100%;
        background-color: rgba($white, .5);
      }
      &.active{
        span{
          background-color: $white;
        }
      }
    }
  }
  &--container{
    align-items: center;
    max-width: $uk-container__lg;
    width: 100%;
    padding: 0 $grid-gutter-width/2;
    margin: auto;
  }
  &--slide{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
    @include media-breakpoint-down(md){
      height: calc(100vh - #{$uk-header-height});
    }
    @include media-breakpoint-down(sm){
      height: 60vh;
    }
    &:after{
      content: '';
      position: absolute;
      display: block;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background-color: rgba(#000, .4);
    }
  }
}
.slider-content{
  max-width: 650px;
  width: 100%;
  position: relative;
  z-index: 999;
  @include media-breakpoint-down(md){
    max-width: 100%;
  }
}
.slider-hero-title{
  display: block;
  color: $uk-primary-color;
  font-weight: bold;
  margin-bottom: 35px;
  line-height: 1.3;
  @include media-breakpoint-down(sm){
    font-size: 20px;
  }

}
.slider-hero-text{
  display: block;
  color: $white;
  font-size: 28px;
  @include media-breakpoint-down(sm){
    font-size: 16px;
  }
}