//  Font Family
$font-family-base: $uk-font-family;

//  Typography
$font-size-base: $uk-body-font-size;
$h1-font-size: $uk-h1-font-size;
$h2-font-size: $uk-h2-font-size;
$h3-font-size: $uk-h3-font-size;

//  Layout
$grid-gutter-width: 48px;
$container-max-widths: (
        xl: 1220px
) !default;
//  Media
$grid-breakpoints: (
        xs: 0,
        sm: 576px,
        md: 768px,
        lg: 1024px,
        xl: 1200px
) !default;
//  Colors
$primary: $uk-primary-color;
$secondary: $uk-secondary-color;
$body-color: $uk-body-text-color;
$link-color: $uk-body-text-color;
$link-hover-color: $uk-primary-color;
$link-hover-decoration: none;

// Buttons
$btn-font-size: $uk-btn-font-size;
$btn-border-radius: $uk-btn-border-radius;
$btn-padding-x: $uk-btn-padding-x;
$btn-padding-y: $uk-btn-padding-y;

$input-border-radius: $uk-btn-border-radius;
$input-padding-x: $uk-input-padding-x;
$input-padding-y: $uk-input-padding-y;