.uk-main{
  max-width: 2000px;
  margin: auto;
  padding-top: $uk-header-height;
  @include media-breakpoint-down(md){
    padding-top: $uk-header-height;
  }
}
.uk-homepage{
  .uk-main{
    padding-top: 0;
  }
}
.uk-section-heading{
  margin-bottom: 55px;
  @include media-breakpoint-down(sm){
    margin-bottom: 35px;
  }
}
.uk-section-title{
  text-transform: uppercase;
  font-weight: bold;
  color: #333333;
  display: flex;
  align-items: center;
  font-size: 27px;
  &:after{
    content: '';
    display: block;
    width: 60px;
    height: 2px;
    background-color: #333333;
    margin-left: 20px;
  }
}
.uk-row{
  margin-bottom: -$grid-gutter-width/2;
  margin-top: -$grid-gutter-width/2;

  >[class*="col"]{
    padding-top: $grid-gutter-width/2;
    padding-bottom: $grid-gutter-width/2;

  }
}
.uk-box-shadow{
  webkit-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.16);
  -moz-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.16);
  box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.16);;
}

.uk-section-has-sidebar{
  padding: 100px 0;
  &--container{
    max-width: $uk-container__lg;
    width: 100%;
    padding: 0 $grid-gutter-width/2;
    margin: auto;
  }
  @include media-breakpoint-down(md){
    padding: $grid-gutter-width/2 0 $grid-gutter-width;
    background: white;
    background: linear-gradient(0deg, white 98%, #eeeeee 100%);
  }
}
.uk-section-full{
  background: white;
  background: linear-gradient(0deg, white 98%, #eeeeee 100%);
  padding: 100px 0;
  @include media-breakpoint-down(md){
    padding: $grid-gutter-width/2 0 $grid-gutter-width;

  }
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: none;
  background-position: right 6px center!important;
  background-repeat: no-repeat!important;
  background-size: 16px!important;
  padding-right: 35px!important;
  overflow: hidden;
}

// Checkboxes, Radios
.uk-checkbox, .uk-radio {
  input.styled:checked + label{
    color: $body-color;
  }
  label{
    user-select: none;
    color: #999999;
    width: 100%;
    margin-bottom: 0;
    cursor: pointer;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    padding-left: 25px;
    font-size: 15px;
    &:hover{
      color:$body-color;
    }
    &::before, &::after{
      content: "";
      display: inline-block;
      position: absolute;
      width: 16px;
      height: 16px;
    }
    &::before {
      left: 0;
    }
  }
  input[type="checkbox"]:checked + label::after{
    font-family: "FontAwesome";
    font-size: 13px;
    content: "\f00c";
  }

  input[type="radio"]:checked + label::after{
    font-family: "FontAwesome";
    font-size: 10px;
    content: "\f111";
  }
}

//  Radio styles
.uk-radio{
  input[type="radio"]{
    opacity: 0;
    z-index: 1;
    &:focus  + label::before{
      outline: thin dotted;
      outline: 5px auto -webkit-focus-ring-color;
      outline-offset: -2px;
    }
    &:checked + label::after{
      -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
      -o-transform: scale(1, 1);
      transform: scale(1, 1);
    }
    &:disabled + label{
      opacity: 0.65;
    }
    &:disabled + label::before{
      cursor: not-allowed;
    }
    + label::after{
      color: $white;
    }
    &:checked{
      + label::before{
        border-color: $uk-primary-color;
        background-color:$white;
        outline: none;
      }
      + label::after{
        background-color:$uk-primary-color;
        color: $uk-primary-color;
        font-size: 12px;
        margin-top: 4px;
        margin-left: 0px;
      }
    }
  }
  label{
    &::before, &::after{
      border-radius: 50%;
    }
    &::before{
      top:4px;
      -webkit-transition: border 0.15s ease-in-out;
      -o-transition: border 0.15s ease-in-out;
      transition: border 0.15s ease-in-out;
      border: 1px solid $uk-primary-color;
      background-color: $white;
    }
    &::after{
      left: 3px;
      top: -1px;
      width: 11px;
      height: 0;
      -webkit-transform: scale(0, 0);
      -ms-transform: scale(0, 0);
      -o-transform: scale(0, 0);
      transform: scale(0, 0);
      -webkit-transition: -webkit-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      -moz-transition: -moz-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      -o-transition: -o-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      transition: transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
    }
  }
}
//  Checkbox styles
.uk-checkbox{
  display: flex;
  align-items: center;
  .styled{
    display: none;
  }
  input[type="checkbox"], input[type="radio"]{
    opacity: 0;
    z-index: 1;
    &:focus{
      + label::before{
        outline: none !important;
      }
    }
    &:checked + label::after{
      -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
      -o-transform: scale(1, 1);
      transform: scale(1, 1);
    }
    &:disabled{
      + label::before{
        background-color: #fff;
        cursor: not-allowed;
      }
    }
    + label::after{
      color: $uk-primary-color;
    }
    &:checked{
      + label::before{
        background-color: #fff;
      }
    }
  }
  label{
    &::before{
      top:4px;
      border: 1px solid #c3c3c3;
      border-radius: 0px;
      -webkit-transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
      -o-transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
      transition: border 0.15s ease-in-out, color 0.15s ease-in-out;
    }

    &::after{
      top: 3px;
      left: 1px;
      -webkit-transform: scale(0, 0);
      -ms-transform: scale(0, 0);
      -o-transform: scale(0, 0);
      transform: scale(0, 0);
      -webkit-transition: -webkit-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      -moz-transition: -moz-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      -o-transition: -o-transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
      transition: transform 0.1s cubic-bezier(0.8, -0.33, 0.2, 1.33);
    }
  }
}
.col-50{
  max-width: 50%;
  flex: 0 0 50%;
}
.col-30{
  max-width: 30%;
  flex: 0 0 30%;
}
.col-70{
  max-width: 70%;
  flex: 0 0 70%;
}
.bottom-bg{
  background-image: url(../images/My-page-decor.png);
  background-repeat: no-repeat;
  background-size: 360px;
  background-position:center bottom;
  @include media-breakpoint-down(md){
    background-size: 980px;
    background-position: center 150px;
  }
  @media screen and (max-width: 480px) {
    background-size: 480px;
    background-position: center bottom;
  }
}
.login-bg{
  background-size: 540px;
  background-position:center bottom;
  @include media-breakpoint-down(md){
    background-size: 480px;
    background-position:center bottom;
  }
  @include media-breakpoint-down(sm){
    background-image: none;
  }
}
.bg-md-none{
  @include media-breakpoint-down(lg){
    background-image: none;
  }
}
