.uk-filter-box{
  margin-top: 80px;
}
.uk-search-field{
  display: flex;
  align-items: center;
  position: relative;
  i{
    position: absolute;
    left: 15px;
  }
  input{
    padding-left: 35px;
    &:focus + i{
      color: $uk-primary-color;
      transition: .2s;
    }
  }
}
.uk-select-field{
  display: flex;
  align-items: center;
  position: relative;
  i{
    position: absolute;
    right: 25px;
  }
  select{
    color: #909497;
    &:focus + i{
      color: $uk-primary-color;
      transition: .2s;
    }
  }
}
.uk-form-field{
  margin-bottom: 22px;
  label{
    display: block;
    margin-bottom: 15px;
  }
}
.uk-btn-field{
  text-align: center;
  margin-top: 75px;
  .btn{
    padding-left: 30px;
    padding-right: 30px;
  }
}
.uk-search-result{
  display: block;
  font-size: 14px;
  &--count{
    font-size: 18px;
    font-weight: bold;
  }
}
.uk-content{
  margin-top: 30px;
}
.mobile-filter-col{
  @include media-breakpoint-down(md){
    animation: menu_open .2s .05s alternate backwards;
    position: absolute;
    padding: 0;
    border-radius: 0;
    left: 0;
    top: $uk-header-height;
    bottom: 0;
    background: white;
    background: linear-gradient(0deg, white 96%, #eeeeee 100%);
    overflow-y: auto;
  }
  .uk-sidebar{
    @include media-breakpoint-down(md){
      border-radius: 0;
      box-shadow: none;
      padding-top: $grid-gutter-width/2;
      height: auto;
    }
  }
  &.active{
    @include media-breakpoint-down(md){
      display: block !important;
      position: absolute;
      z-index: 80;
      padding: 0;
      border-radius: 0;
    }
  }
}
.filter-close{
  margin-left: 10px;
  span{
    display: block;
    background-color: $uk-primary-color;
    width: 25px;
    height: 2px;
    &:first-child{
      transform: rotate(-45deg);
    }
    &:last-child{
      transform: rotate(45deg);
      margin-top: -2px;
    }
  }
}
.uk-mobile-filter{
  color: $uk-primary-color;
  display: flex;
  align-items: center;
  cursor: pointer;
  i{
    color: #fff;
    background-color: $uk-primary-color;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    font-size: 20px;
  }
}
@keyframes menu_open {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}